<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ HeaderName }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="ReqDocument" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-ToEmail="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span
                                            class="input-group-text bg-white text-dark font-weight-light bg-white">To:</span>
                                    </div>
                                    <input type="text" v-model="txtEmailTo" class="form-control  bg-white"
                                        disabled="disabled" placeholder="zoe@yopmail.com">
                                    <div class="input-group-append">
                                        <span class="input-group-text bg-white  text-info ">
                                            <a href="javascript:;" v-on:click="IsShowHiddenField('isHidden')"
                                                class="text-info cc">{{ $t('CCuppercase') }}</a>
                                        </span>
                                    </div>
                                    <div class="input-group-append">
                                        <span class="input-group-text bg-white">
                                            <a href="javascript:;" v-on:click="IsShowHiddenField('isHide')"
                                                class="text-info bcc">{{ $t('BCCuppercase') }} </a>
                                        </span>
                                    </div>
                                </div>
                            </template>
                            <template v-slot:tgslot-CC="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text bg-white  text-dark font-weight-light">{{
                                            $t('CCuppercase') }}:</span>
                                    </div>
                                    <input type="text" id="emails" name="CCemail" v-model="txtEmailCc"
                                        class="form-control  box-shadow bg-white" @change="validateCCEmail"
                                        :class="{ 'form-control': true, 'is-invalid': isInvalidCCEmail }"
                                        placeholder="Multiple emails can be separated by comma or semicolon.">
                                    <span v-show="isInvalidCCEmail" class="invalid-feedback"> {{ $t('InvalidEmailFormat')
                                    }}</span>
                                </div>
                            </template>
                            <template v-slot:tgslot-BCC="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text bg-white  text-dark font-weight-light">{{
                                            $t('BCCuppercase') }}:</span>
                                    </div>
                                    <input type="text" id="emails_bcc" v-model="txtEmailBcc" name="BCCemail"
                                        class="form-control box-shadow bg-white" @change="validateBCCEmail"
                                        :class="{ 'form-control': true, 'is-invalid': isInvalidBCCEmail }"
                                        placeholder="Multiple emails can be separated by comma or semicolon.">
                                    <span v-show="isInvalidBCCEmail" class="invalid-feedback"> {{ $t('InvalidEmailFormat')
                                    }}</span>
                                </div>
                            </template>
                            <template v-slot:tgslot-FromEmail="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text bg-white text-dark font-weight-light">{{
                                            $t('From') }}:</span>
                                    </div>
                                    <input type="text" v-model="txtEmailFrom" class="form-control box-shadow bg-white"
                                        disabled="disabled" placeholder="james149adams@gmail.com">
                                </div>
                            </template>
                            <template v-slot:tgslot-ReplyTo="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text bg-white text-dark font-weight-light ">{{
                                            $t('ReplyTo') }}:</span>
                                    </div>
                                    <input type="text" v-model="txtEmailReplyTo" name="ReplyToEmail"
                                        class="form-control box-shadow bg-white" v-validate="mailvalidate">
                                 </div>
                            </template>
                            <template v-slot:tgslot-Subject="{ data }">
                                <div class="input-group mb-3">
                                    <div class="input-group-prepend">
                                        <span class="input-group-text bg-white text-dark font-weight-light" id="">{{
                                            $t('Subject') }}:</span>
                                    </div>
                                    <input type="text" v-model="txtEmailSubject" class="form-control box-shadow bg-white">
                                </div>
                            </template>
                            <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" v-model="noteDescription"
                                    :class="{ 'is-invalid': data.error != '' }">
                                </ckeditor>
                                <span class="invalid-feedback">{{ data.error }}</span>
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
        <AddHoldReasonPopup v-if="AddHoldReason"></AddHoldReasonPopup>
        <AddReasonPopup v-if="AddReason"></AddReasonPopup>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import AddReasonPopup from "../CandidateProfile/AddReasonPopup.vue"
import AddHoldReasonPopup from "../CandidateProfile/AddHoldReasonPopup.vue"
export default {
    props: {
        OnHoldClick: {
            type: Boolean,
            required: true
        },
    },
    components: { AddReasonPopup, AddHoldReasonPopup },
    data() {
        return {
            isLoading: false,
            AddReason: false,
            AddHoldReason: false,
            IsVendor: '',
            ApplicantId: 0,
            JobId:0 ,
            editor: this.CLASSIC_EDITOR,
            isHidden: false,
            notify: '',
            txtEmailCc: '',
            txtEmailBcc: '',
            InvalidCCEmail: '',
            InvalidBCCEmail: '',
            txtEmailFrom: '',
            isInvalidCCEmail: false,
            isInvalidBCCEmail: false,
            txtEmailReplyTo: '',
            HeaderName:'',
            txtEmailSubject: '',
            isHide: false,
            CandidateData: [],
            JobsData: [],
            jobtitle: '',
            jobtype: '',
            txtEmailTo: '',
            userId: '',
            companyId: '',
            noteDescription: '',
            FormSchema: [
            {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('RejectResonReq'),
                            name: "RejectReason",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon : true,
                                onAddButtonClick : this.openaddreasonpopup,
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Notify'),
                            name: "Notify",
                            value: "",
                            config: {
                                options: [
                                    { name: 'Candidate', value: "option1" }, { name: 'Hiring Team', value: "option2" },
                                ],
                                onChange: this.GetEmailForm
                            },
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('LeaveComment'),
                            name: "LeaveComment",
                            value: "",
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Template'),
                            name: "SelectTemplate",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.onChangeTemplate
                            },
                            placeholder: this.$t('SelectTemplate'),
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "ToEmail",
                            value: "",
                            placeholder: this.$t('Enter To Email'),
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "CC",
                            value: "",
                            visibility: false,
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "BCC",
                            value: "",
                            visibility: false,
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "FromEmail",
                            value: "",
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "ReplyTo",
                            value: "",
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "Subject",
                            value: "",
                            placeholder: this.$t('Enter Subject'),
                            validationRules: { "required": false },
                            visibility: false
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Note Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            visibility: true,
                            validationRules: { "required": false, max: 5000 },
                            visibility: false
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-upload pr-2'></i>Update",
                    isVisible: true,
                    onClick: this.OnSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.IsVendor = this.GetUserInfo.IsVendor;
        vm.txtEmailFrom = this.GetUserInfo.Email;
        vm.ApplicantId = this.$route.params.id;
        vm.JobId = this.$route.params.JobId;
        await this.getForm(vm.ApplicantId);
        this.GetDdlRecords_Template();
        this.emailValidate = {
            required: true,
            regex: /^(\s?[^\s,]+[^\s,]+\.[^\s,]+\s?,)*(\s?[^\s,]+[^\s,]+\.[^\s,]+)$/
        };
        this.mailvalidate = {
            regex: /^\w+([\.-]?\w+)*@@\w+([\.-]?\w+)*(\.\w{2,3})+$/
        }
        if(vm.OnHoldClick==true){
            vm.FormSchema[0].Data[0].label= this.$t('On Hold Reason');
            vm.HeaderName='Hold Candidate';
            this.GetOnHoldReasonList();
        }else{
            vm.FormSchema[0].Data[0].label= this.$t('RejectResonReq');
            vm.HeaderName='Reject Candidate';
            this.GetRejectReasonList();
        }
    },
    methods: {
        getForm: function (id) {
            var vm = this;
            vm.isLoading = true;
            var url = `applicantId=${id}&moduleName=Hiring&subModuleCode=HR_TALENT_POOL&langCode=en`;
            DataServices.CandidateDetails(url).then(function (response) {
                if (response != null && response.data != null) {
                    let CandidateData = response.data.data.filter(function (e) {
                        e.notice_period = e.notice_period + ' ' + e.notice_period_type;
                        return true;
                    });
                    vm.CandidateData = CandidateData;
                    vm.txtEmailTo = vm.CandidateData[0].email_id;
                    vm.isLoading = false;
                } else {
                    vm.CandidateData = [];
                }
            });
        },
        GetDdlRecords_Template: async function () {
            var vm = this;
            var url = `ids=&&&type=EMAILTEMPLATE_HR&search=&refId=`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[2].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    vm.$refs.ReqDocument.UpdateKeyValue();
                }
            });
        },
        GetRejectReasonList: async function () {
            var vm = this;
            vm.FormSchema[0].Data[0].config.options=[];
            var url = `ids=&&&type=REJECT_REASON&search=&refId=`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    vm.$refs.ReqDocument.UpdateKeyValue();
                }
            });
        },
        GetOnHoldReasonList: async function () {
            var vm = this;
            vm.FormSchema[0].Data[0].config.options=[];
            var url = `ids=&&&type=ACCEPT_REASON&search=&refId=`;
            await DataServices.GetDDLData(url).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                    vm.$refs.ReqDocument.UpdateKeyValue();
                }
            });
        },
        onChangeTemplate: async function (x, y, z) {
            var vm = this;
            var url = `templateId=${y.value}`;
            await DataServices.GetEmailTemplate(url).then(
                function (response) {
                    if (response.data.DATA.length > 0) {
                        var email = response.data.DATA[0].template_content;
                        var emailSubject = response.data.DATA[0].template_subject;
                        vm.noteDescription = email;
                    }
                }
            );
        },
        Close: function () {
            this.$parent.RejectCandidate = false;
        },
        openaddreasonpopup: function (){         
           var vm=this; 
           if(vm.OnHoldClick==true){
                vm.AddHoldReason = !vm.AddHoldReason;   
           }else{
                vm.AddReason = !vm.AddReason;   
           } 
        },
        GetEmailForm: function (x,y,z) {
            var vm=this;
            if(y.value.length>0){
                vm.FormSchema[2].Data[0].visibility = true;
                vm.FormSchema[2].Data[0].validationRules = { "required": true };
                vm.FormSchema[3].Data[0].visibility = true;
                vm.FormSchema[6].Data[0].visibility = true;
                vm.FormSchema[7].Data[0].visibility = true;
                vm.FormSchema[8].Data[0].visibility = true;
                vm.FormSchema[9].Data[0].visibility = true;
            }else{
                vm.FormSchema[2].Data[0].visibility = false;
                vm.FormSchema[2].Data[0].validationRules = { "required": false };
                vm.FormSchema[3].Data[0].visibility = false;
                vm.FormSchema[6].Data[0].visibility = false;
                vm.FormSchema[7].Data[0].visibility = false;
                vm.FormSchema[8].Data[0].visibility = false;
                vm.FormSchema[9].Data[0].visibility = false;
            }
        },
        IsShowHiddenField: function (type) {
            var vm = this;
            if (type == 'isHidden') {
                vm.isHidden = !vm.isHidden
                vm.isHidden == true ? vm.FormSchema[4].Data[0].visibility = true : vm.FormSchema[4].Data[0].visibility = false;
            } else if (type == 'isHide') {
                vm.isHide = !vm.isHide
                vm.isHide == true ? vm.FormSchema[5].Data[0].visibility = true : vm.FormSchema[5].Data[0].visibility = false;
            }
        },
        validateCCEmail: function () {
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            this.isInvalidCCEmail = !emailRegex.test(this.txtEmailCc);
        },
        validateBCCEmail: function () {
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            this.isInvalidBCCEmail = !emailRegex.test(this.txtEmailCc);
        },
        onSubmit:async function (formfields, val) {
            var vm = this;
            var TypeValue='';
            vm.OnHoldClick==true? TypeValue='HOLD':TypeValue='REJECT';
            var isSendEmail = false;
            var IsNotifyCandidate=false;
            var IsNotifyHiring=false;
            if (formfields.Notify !='') {
                isSendEmail = true;
                IsNotifyCandidate=true;
                IsNotifyHiring=true;
            }
            var rejectReasonData = {
                APPLICANTID_VAL: vm.ApplicantId,
                REASON_VAL: formfields.RejectReason,
                POST_ID: vm.JobId,
                USER_ID: vm.userId,
                COMMENTS_VAL: formfields.LeaveComment,
                IS_NOTIFY_CANDIDATE_VAL: IsNotifyCandidate,
                IS_NOTIFY_HIRING_TEAM_VAL: IsNotifyHiring,
                TYPE_VAL: TypeValue,
                RATING_VAL: 0,
                Is_Rating: false,
                is_SendEmail: isSendEmail,
                CompanyId:  vm.companyId
            };
            var objectPostString = JSON.stringify(rejectReasonData)
            await DataServices.ManageApplicantStatus(objectPostString).then((response) => {
            if (response.data.status == "Success") {
                if (formfields.Notify =='') {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("CandidateProfile")]), "success", true, vm.$t("Alert"));
                    if(vm.OnHoldClick==true){
                        vm.$parent.OnHoldClass='';
                        vm.$parent.IsReject=true;
                        vm.$parent.IsActive=true;
                        vm.$parent.IsReject=true;
                        vm.$parent.IsHired =true;
                        vm.$parent.IsOnHold =false;
                        vm.$parent.OnHoldClass='p-action-btn text-white rqst-doc disabled';
                    }else{
                        vm.$parent.IsReject=true;
                        vm.$parent.IsActive=true;
                        vm.$parent.IsReject=false;
                        vm.$parent.IsHired =true;
                        vm.$parent.IsOnHold =true;
                    } 
                    vm.Close();
                }else{
                      var emailData = {
                        CandidateID: vm.ApplicantId,
                        UserId: vm.userId,
                        CompanyId: vm.companyId,
                        TemplateId: formfields.SelectTemplate,
                        id: formfields.SelectTemplate,
                        postId: vm.JobId,
                        EmailTo: vm.txtEmailTo,
                        EmailCc: vm.txtEmailCc,
                        EmailBcc: vm.txtEmailBcc,
                        EmailFrom: vm.txtEmailFrom,
                        EmailReplyTo: '',
                        EmailSubject: vm.txtEmailSubject,
                        EmailDesc: vm.noteDescription,
                        Notify: vm.notify,
                        ReqFrom: "REQUEST_DOC"
                    };
                    var Params = JSON.stringify(emailData);
                    DataServices.SendEmail(Params).then((response) => {
                        if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t("EmailSuccessfullySend"), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"),'failure',true,vm.$t('Alert'));
                        }
                    }, response => {
                    });
                }
                
            }
            else {
                vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
            }
        }, response => {
        });
    }
    }
}
</script>