<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <!-- start page heading -->
            <div class="tab-pane float-left col-12 mb-0 p-0" id="tab_pre_screen_assesment">
                <div class="col-lg-12 mt-0 px-0">
                    <div class="heading-border mb-2">
                        <h5 class="h5"><span>{{ $t('ScreeningAssignment') }}</span></h5>
                    </div>
                </div>
                <div class="float-left mb-0 p-0 col-lg-12" v-if="questionaireData != null && !noRecord">
                    <div v-for="(question, index) in questionaireData" class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                            <label><span class="text-danger font-weight-bold">*</span> {{ question.QUESTTION_TEXT }}
                                ?</label>
                            <div class="form-group" style="margin-left:10px;">
                                <div v-if="question.ANSWER_TYPE == 'Objective'" class="row">
                                    <span v-for="(option, indexo) in question.OPTION_VALUE"
                                        class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                        <span class="custom-control custom-radio">
                                            <input type="radio" v-model="question.questionaire_answer"
                                                v-validate="'required'" v-bind:id="'customcheckbox' + index + indexo"
                                                v-bind:value="option" v-bind:class="{ 'custom-control-input': true }"
                                                v-bind:name="question.QUESTION_ID" disabled>
                                            <label class="custom-control-label "
                                                v-bind:for="'customcheckbox' + index + indexo">
                                                {{ option }}
                                            </label>
                                        </span>
                                    </span>
                                </div>

                                <div v-if="question.ANSWER_TYPE == 'Star Rating'" class="row">
                                    <span class="givenrating col-sm-12 col-md-12 col-lg-12 col-xl-12"
                                        v-on:click="StarCheck($event, question)">
                                        <span id="1"
                                            v-bind:class="{ 'fa fa-star checked': question.questionaire_answer >= 1 && question.questionaire_answer != 0, 'fa fa-star': question.questionaire_answer < 1 || question.questionaire_answer == 0 }"></span>
                                        <span id="2"
                                            v-bind:class="{ 'fa fa-star checked': question.questionaire_answer >= 2 && question.questionaire_answer != 0, 'fa fa-star': question.questionaire_answer < 2 || question.questionaire_answer == 0 }"></span>
                                        <span id="3"
                                            v-bind:class="{ 'fa fa-star checked': question.questionaire_answer >= 3 && question.questionaire_answer != 0, 'fa fa-star': question.questionaire_answer < 3 || question.questionaire_answer == 0 }"></span>
                                        <span id="4"
                                            v-bind:class="{ 'fa fa-star checked': question.questionaire_answer >= 4 && question.questionaire_answer != 0, 'fa fa-star': question.questionaire_answer < 4 || question.questionaire_answer == 0 }"></span>
                                        <span id="5"
                                            v-bind:class="{ 'fa fa-star checked': question.questionaire_answer >= 5 && question.questionaire_answer != 0, 'fa fa-star': question.questionaire_answer < 5 || question.questionaire_answer == 0 }"></span>
                                    </span>
                                    <input type="hidden" v-bind:name="question.QUESTION_ID" v-validate="'required|max:5'"
                                        v-model="question.questionaire_answer" />
                                </div>

                                <div v-if="question.ANSWER_TYPE == 'Comments'" class="row">
                                    <span class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                        <input type="text" disabled v-bind:class="{ 'form-control': true }"
                                            v-validate="'required'" v-model="question.questionaire_answer"
                                            v-bind:name="question.QUESTION_ID" />
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="noRecord" class="col text-center">
                    <span class="text-center text-danger no-record" style="padding: 20px;">{{ $t('NoRecordfound')
                    }}</span>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
export default {
    components: {},
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FromType: '',
            questionaireData: [],
            ShowAddEditFeedback: false,
            ShowFeedBackHistory: false,
            SortBy: '',
            SortExp: '',
            dynamicSrc: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            RecuiterId: 0,
            InterviewStageId: 0
        }
    },
    created: async function () {
        var vm = this;
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containername = this.GetUserInfo.ContainerName;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&postId=${vm.JobId}`;
            await DataService.GetCandidateQuestionaireData(url).then(
                function (response) {
                    vm.questionaireData = [];
                    if (response.data != null && response.data.code !='0') {
                        if (response.data != "" && response.data.result.length > 0) {
                            vm.questionaireData = response.data.result;
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.questionaireData = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.questionaireData = [];
                    }
                });
        },

        Close: function () {
            var vm = this;
            this.$router.push({ name: 'CandidateList' });
        },
    },
}
</script>