<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddReason') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                        </dynamic-form>
                    </div>  
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
    },
    data() {
        return {
            isLoading: false,
            CompanyId: null,
            Userid: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                    {
                            astype: "TextField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        vm.ApplicantId = this.$route.params.id;
    },
    methods: {
        onSubmit(formfields, val) {
            var vm = this;
            var data = {
                    id:0,
                    Reasontxt: formfields.Reason,
                    CompanyId: vm.CompanyId,
                    ReasonTypetxt:'Accept',
                    CreatedBy: vm.Userid,
             };
            var objectPostString = JSON.stringify(data)
            DataServices.SaveReasons(objectPostString).then((response) => {
                if (response.data.Status == 'Success') {
                    if (response.data.Msg == 'ALREADY EXISTS' ||response.data.Msg == 'ERROR') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Reason")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Reason")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.FetchData();
                    }
                }else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Reason")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            var vm=this;
            vm.$parent.GetOnHoldReasonList();
            vm.$parent.AddHoldReason = false;
        },
    }
}
</script>