<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Feedback') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form ref="AddClient" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-StarField="{ data }">
                                <div class="star-rating" :value="rating" v-on:click="StarCheck">
                                    <s v-bind:class="{ 'active': rating != 0 }" id="1">
                                        <s id="2" v-bind:class="{ 'active': rating >= 2 && rating != 0 }">
                                            <s id="3" v-bind:class="{ 'active': rating >= 3 && rating != 0 }">
                                                <s id="4" v-bind:class="{ 'active': rating >= 4 && rating != 0 }">
                                                    <s id="5" v-bind:class="{ 'active': rating >= 5 && rating != 0 }">
                                                    </s>
                                                </s>
                                            </s>
                                        </s>
                                    </s>
                                </div>
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
        RecuiterId: {
            type: Number,
            required: true
        },
        InterviewStageId: {
            type: Number,
            required: true
        },
        FeedbackData: {
            type: Object,
            required: true
        },
        FromType: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            rating: 0,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Comment'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            name: "StarField",
                            value: "",
                            validationRules: { "required": false },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('Preference'),
                            name: "Preference",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Recommended', value: 'recommended'
                                }, {
                                    name: "Don't Recommend", value: 'not_recommended'
                                }, {
                                    name: 'Strongly Recommend', value: 'strongly_recommended'
                                }],
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
            userId: 0,
            companyId: 0
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            if (vueObj.FeedbackData.applicant_id != undefined) {
                (vueObj.FormSchema)[0].Data[0].value = vueObj.FeedbackData.comment;
                (vueObj.FormSchema)[1].Data[0].value = vueObj.FeedbackData.rating;
                (vueObj.FormSchema)[2].Data[0].value = vueObj.FeedbackData.feedback;
                vueObj.rating = vueObj.FeedbackData.rating;
                if (vueObj.FromType == 'OpenView') {
                    vueObj.FormSchema[0].Data[0].disabled = true;
                    vueObj.buttons[0].isVisible = false;
                }
            }else{
                (vueObj.FormSchema)[2].Data[0].value='recommended';
            }
        },
        Close: function () {
            this.$parent.ShowAddEditFeedback = false;
        },
        StarCheck: function (event) {
            var vm = this
            vm.rating = event.target.id;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                RATING: vm.rating,
                feedback: formfields.Preference,
                COMMENT: formfields.Description,
                POST_ID: vm.JobId,
                APPLICANT_ID: vm.ApplicantId,
                USER_ID: vm.userId,
                COMPANY_ID: vm.companyId,
                INTERVIEWER_ID: vm.RecuiterId,
                INTERVIEWSTAGE_ID: vm.InterviewStageId
            };
            var params = JSON.stringify(jsonobj);
            DataServices.AddFeedback(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Feedback")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.CrewId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Feedback")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Feedback")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchData();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Feedback")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>