                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div class="card mb-3">
                            <div class="card-header">
                                <a class="card-link" data-toggle="collapse" href="#collapseOne77">
                                    <div class="conversation-date heading-border"><span><em
                                                class="fa fa-wpforms text-danger pr-2"></em>{{ $t('Channel') }}</span></div>
                                </a>
                            </div>
                            <div id="collapseOne77" class="collapse show" data-parent="#accordion">
                                <div class="card-body">
                                    <form>

