                                                </div>
                                            </div>
                                            <div class="col-lg-12 float-left  "
                                                v-if="this.candidateData[0].channel_name == null">
                                                <span class="w-100 text-danger d-inline-block p-2 border text-center ">
                                                    {{ $t('NoRecordfound') }}
                                                </span>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div class="card mb-3">
                            <div class="card-header">
                                <a class="card-link" data-toggle="collapse" href="#collapseOne7">
                                    <div class="conversation-date heading-border"><span><em
                                                class="fa fa-shopping-basket text-danger pr-2"></em>{{ $t('Source')
                                                }}</span></div>
                                </a>
                            </div>
                            <div id="collapseOne7" class="collapse show" data-parent="#accordion">
                                <div class="card-body">
                                    <form>
                                        <div class="row">
                                            <div class="col-lg-3">
                                                <div class="form-group">
                                                    <span class="form-control px-0 bg-white border-0">
                                                        <span class="form-control px-0 bg-white border-0">{{
                                                            candidateData[0].source_name }}</span>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        <div class="card mb-3">
                            <div class="card-header">
                                <a class="card-link" data-toggle="collapse" href="#collapseOne9">
                                    <div class="conversation-date heading-border"><span><em
                                                class="fa fa-paperclip text-success pr-2"></em>{{ $t('Attachments')
                                                }}</span></div>
                                </a>
                            </div>
                            <div id="collapseOne9" class="collapse show" data-parent="#accordion">
                                <div class="card-body">
                                    <form>
                                        <div class="row" v-cloak>
                                            <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3 float-left"
                                                v-for="(doc, index) in documentList">
                                                <div class="box border pt-4 text-center">
                                                    <div class="img-ico text-center pb-4 file-image doc-icon pt-3"
                                                        style="min-height:162px;">
                                                        <img v-if="doc.path != null && doc.type == 'jpg' || doc.type == 'png' || doc.type == 'jpeg' || doc.type == 'tiff' || doc.type == 'bmp' || doc.type == 'dib' || doc.type == 'ico' || doc.type == 'jfif' || doc.type == 'jpe' || doc.type == 'gif' && doc.type == 'tif'"
                                                            v-bind:src="doc.resume_path" alt="DefaultImage" />
                                                        <span v-else-if="doc.type == 'xlsx' || doc.type == 'xls'">
                                                            <em class="fa fa-file-excel-o mt-4 pt-1"
                                                                aria-hidden="true"></em>
                                                        </span>
                                                        <span v-else-if="doc.type == 'pdf'">
                                                            <em class="fa fa-file-pdf-o text-danger mt-4 pt-1"
                                                                aria-hidden="true"></em>
                                                        </span>
                                                        <span v-else-if="doc.type == 'docx' || doc.type == 'doc'">
                                                            <em class="fa fa-file-word-o text-primary mt-4 pt-1"
                                                                aria-hidden="true"></em>
                                                        </span>
                                                        <span v-else-if="doc.type == 'csv'">
                                                            <em class="fa fa-file-text-o text-primary mt-4 pt-1"
                                                                aria-hidden="true"></em>
                                                        </span>
                                                        <span v-else>
                                                            <em class="fa fa-file text-primary mt-4 pt-1"
                                                                aria-hidden="true"></em>
                                                        </span>
                                                    </div>
                                                    <hr class="mb-0">
                                                    <div class="clearfix"></div>
                                                    <div class="row mx-0 my-2">
                                                        <div :title="doc.name"
                                                            class="col-md-12 text-left text-truncate font-14 mb-2 font-weight-bold">
                                                            {{ doc.name }}    
                                                        </div>
                                                        <div class="verticle-action">
                                                            <em aria-hidden="true" class="fa fa-ellipsis-v action_icon"
                                                            @click="openActionIcon($event)"></em>
                                                            <span class="action-list-box noneClass"
                                                                :id="'spnSlider' + index">
                                                                <span id="action-list" class="list-actions"
                                                                    style="padding-left: 0px;">
                                                                
                                                                    <a data-toggle="modal"
                                                                        v-on:click="getdownloadfile( doc.path, '', doc.name, doc.file_desc)"
                                                                        class="actions-onclick edit-list"><em
                                                                            aria-hidden="true"
                                                                            class="fa fa-download"></em><br>Download</a>
                                                                    <em aria-hidden="true"
                                                                        class="fa fa-times close close-action"></em>
                                                                </span>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
export default {
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    components: {

    },
    data() {
        return {
            isLoading: false,
            StageMsg: false,
            showStages: false,
            ScheduleInterview: false,
            formattedDate: '',
            SortBy: '',
            SortExp: '',
            dynamicSrc:'',
            PageSize: "10",
            PageNumber: "1",
            Fromtype: '',
            TotalRecords: 0,
            Headers: null,
            containername:null,
            candidateData: [],
            stageData: [],
            statusData: [],
            documentList: [],
            stageLength: 0,
            documentList: [],
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            rqstdocSeen: false,
            candidate_profile_path: "~/Content/images/DefaultImages/avatar-icon.jpg",
        }
    },
    created: async function () {
        var vm = this;
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containername = this.GetUserInfo.ContainerName;
        await vm.FetchData();
        await vm.GetCandidateData();
        await vm.GetCandidateStatusProcessData();
        await vm.getDocumentList();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&departmentIds=0&skillIds=0&langCode=EN&jobID=${vm.JobId}`;
            await DataService.GetCandidateDetails(url).then(async function (response) {
                    vm.candidateData = [];
                    if (response.data != null && response.data != "" && response.data.data.length>0) {
                        vm.candidateData = response.data.data;
                        if(vm.candidateData[0].profile_path){    
                            vm.BindProfileSrc(vm.candidateData[0].profile_path)
                        }
                        vm.$parent.CandidateEmail=vm.candidateData[0].email_id;
                        if (vm.candidateData[0].date_of_birth != null) {
                            var dateString = vm.candidateData[0].date_of_birth;
                            var date = new Date(dateString);
                            var day = date.getDate().toString().padStart(2, '0');
                            var month = (date.getMonth() + 1).toString().padStart(2, '0');
                            var year = date.getFullYear();
                            vm.formattedDate = `${day}/${month}/${year}`;
                        }
                    }
                });
        },
        GetCandidateStatusProcessData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&jobid=${vm.JobId}`;
            await DataService.GetStatusCandidate(url).then(
                function (response) {
                    vm.statusData = [];
                    if (response.data != null && response.data != "") {
                        vm.statusData = response.data.data;
                    }
                });
        },
        getDocumentList: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN`;
            await DataService.GetDocumentsList(url).then(async function (response) {
                if (response.data != null) {
                    vm.Headers = response.data.schema;
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.documentList = response.data.data;
                        for (const doc of vm.documentList) {
                            doc.resume_path = await vm.BindSrc(doc.path);
                        }
                    } else {
                        vm.documentList = [];
                    }
                }
                else {
                    vm.documentList = [];
                }
            })
        },
        JobDeatailRedirection:function(){
            var vm=this;
            vm.$router.push({ name: 'JobPost', params: { id: vm.JobId, type: 'JobPostDetail' } });
        },
        getdownloadfile: async function (path, type, name, attachement_mime) {
            var vm = this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("FileNotFoundForDownload"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        openActionIcon(e){
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        BindSrc: async function (filepath) {
            var vm = this;
            var dynamicSrc = '';
            var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
            var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
            let Obj = {
                path: path,
                fileName: fileName
            };
            var url = JSON.stringify(Obj);
            await DataService.GetFileSrc(url).then((response) => {
                if (response.data != '') {
                    dynamicSrc = response.data
                }
            });
            return dynamicSrc;
        },
    changeStatus: async function (stageId) {
        var vm = this;
        var data = {
            applicantId: vm.ApplicantId,
            stageId: stageId,
            modifiedBy: vm.userId,
            jobid: vm.JobId
        }
        vm.confirmR(vm.$t("AreYouSureWantToUpdateStage"), true, vm.$t('Stage'), async function () {
            var objectPostString = JSON.stringify(data);
            await DataService.UpdateCandidateStage(objectPostString).then(
                function (response) {
                    if (response.data[0].Status == "Success") {
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Stage")]), "success", true, vm.$t("Alert"));
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("Stage")]), "failure", true, vm.$t("Alert"));
                    }
                }, function (response) {
                });
        });
    },
    BindProfileSrc: async function (filepath) {
            var vm = this;
            var dynamicSrc = '';
            var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
            var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
            let Obj = {
                path: path,
                fileName: fileName
            };
            var url = JSON.stringify(Obj);
            await DataService.GetFileSrc(url).then((response) => {
                if (response.data != '') {
                    vm.candidate_profile_path = response.data
                }
            });
            return dynamicSrc;
        },
    GetCandidateData: async function () {
        var vm = this;
        var url = `applicantId=${vm.ApplicantId}&jobid=${vm.JobId}`;
        await DataService.GetStages(url).then(
            function (response) {
                if (response.data != null) {
                    vm.Headers = response.data.schema;
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.stageData = response.data.data;
                        vm.stageLength = vm.stageData.length;
                        vm.noRecord = false;
                        vm.StageMsg = false;
                        vm.ScheduleInterview = true;
                        vm.showStages = true;
                    } else {
                        vm.noRecord = true;
                        vm.stageData = [];
                        vm.TotalRecords = 0
                        vm.StageMsg = true;
                        vm.showStages = true;
                        vm.ScheduleInterview = false;
                    }
                }
                else {
                    vm.noRecord = true;
                    vm.StageMsg = true;
                    vm.showStages = true;
                    vm.ScheduleInterview = false;
                    vm.stageData = [];
                }
            });
    },
    Close: function () {
        var vm = this;
        this.$router.push({ name: 'CandidateList' });
    },
},
}
</script>
