<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddRejectReason') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        ReasonArr: {
            type: Object,
            required: true
        },
        ReasonId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            docId: 0,
            userId: 0,
            companyId: 0,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('RejectReason'),
                            name: "RejectReason",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "CheckBoxField",
                            label: this.$t('ReasonType'),
                            name: "ReasonType",
                            value: "",
                            config: {
                                options: [
                                    { name: 'Visiblity at store front', value: "true" }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm=this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
            if (vm.ReasonId > 0) {
                (vm.FormSchema)[0].Data[0].value = vm.ReasonArr.row.reason;
                (vm.FormSchema)[0].Data[1].value = vm.ReasonArr.row.reason_type=='Reject'?"true":'';
            }
        },
        Close: function () {
            this.$parent.AddPostReasonPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            if (formfields.ReasonType != "") {
                var data = {
                    RejectReasontxt: formfields.RejectReason,
                    CompanyId: vm.companyId,
                    RejectReasonTypetxt: 'Reject',
                    CreatedBy: vm.userId,
                    Id: vm.ReasonId
                }
            } else {
                var data = {
                    RejectReasontxt: formfields.RejectReason,
                    CompanyId: vm.companyId,
                    RejectReasonTypetxt: 'HideHrReason',
                    CreatedBy: vm.userId,
                    Id: vm.ReasonId
                };
            }
            var objectPostString = JSON.stringify(data);
            DataServices.SaveRejectReasons(objectPostString).then((response) => {
                if (response.data.Status == 'Success') {
                    if(vm.ReasonId>0){
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("RejectReason")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.FetchData();
                    }else{
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("RejectReason")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                        vm.$parent.FetchData();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("RejectReason")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>