<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('FeedbackHistory') }} </h4>
                        <button type="button" class="close" v-on:click="Close"><i class="fa fa-times"
                                aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="table-responsive">
                            <table id="example" class="table table-bordered mb-0" style="width:100%;">
                                <thead class="thead-bg">
                                    <tr>

                                        <th style="min-width:150px;"><span> {{ $t('Comment') }}</span></th>
                                        <th style="min-width:150px;"><span>  {{ $t('Rating') }}</span></th>
                                        <th style="min-width:150px;"><span> {{ $t('Feedback') }}  </span></th>
                                        <th style="min-width:150px;"><span> {{ $t('ProvidedBy') }}  </span></th>
                                        <th style="min-width:150px;"><span>{{ $t('Date') }} </span></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in feedbackhistory">
                                        <td>{{ item.COMMENT }}</td>

                                        <td class="text-primary ">
                                            <span class="givenrating col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                                <span id="1"
                                                    v-bind:class="{ 'fa fa-star checked': item.RATING >= 1 && item.RATING != 0, 'fa fa-star': item.RATING < 1 || item.RATING == 0 }"></span>
                                                <span id="2"
                                                    v-bind:class="{ 'fa fa-star checked': item.RATING >= 2 && item.RATING != 0, 'fa fa-star': item.RATING < 2 || item.RATING == 0 }"></span>
                                                <span id="3"
                                                    v-bind:class="{ 'fa fa-star checked': item.RATING >= 3 && item.RATING != 0, 'fa fa-star': item.RATING < 3 || item.RATING == 0 }"></span>
                                                <span id="4"
                                                    v-bind:class="{ 'fa fa-star checked': item.RATING >= 4 && item.RATING != 0, 'fa fa-star': item.RATING < 4 || item.RATING == 0 }"></span>
                                                <span id="5"
                                                    v-bind:class="{ 'fa fa-star checked': item.RATING >= 5 && item.RATING != 0, 'fa fa-star': item.RATING < 5 || item.RATING == 0 }"></span>
                                            </span>
                                        </td>
                                        <td v-if="item.FEEDBACK == 'recommended'"> {{ $t('Recommended') }} </td>
                                        <td v-if="item.FEEDBACK == 'not_recommended'">  {{ $t('NotRecommend') }}</td>
                                        <td v-if="item.FEEDBACK == 'strongly_recommended'"> {{ $t('StronglyRecommend') }}</td>
                                        <td>{{ item.USER_NAME }}</td>
                                        <td>{{ item.DATE }}</td>
                                    </tr>

                                </tbody>
                            </table>

                        </div>
                    </div>
                    <div class="modal-footer">
                        <a href="javascript:;" v-on:click="Close" class="btn btn-danger formbtn">
                            <i class="fa fa-times"></i> {{ $t('Close') }}
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    components: {},
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
        RecuiterId: {
            type: Number,
            required: true
        },
        InterviewStageId: {
            type: Number,
            required: true
        },
        FeedbackData: {
            type: Object,
            required: true
        },
        FromType: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            noRecords: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            feedbackhistory: [],
            user_type: null,
            user_id: null,
            companyId: null,
            search: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.getfeedbackhistory();
    },
    methods: {
        getfeedbackhistory: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&postId=${vm.JobId}&interviewerid=${vm.RecuiterId}&stageid=${vm.InterviewStageId}&langCode=EN`;
            await DataServices.GetFeedbackHistory(url).then(
                function (response) {
                    if (response.data != null) {
                        if (response.data != "" && response.data.DATA.length > 0) {
                            vm.feedbackhistory = response.data.DATA
                        }
                        else {
                            vm.noRecord = true;
                        }
                    }
                });
        },
        Close: function () {
            this.$parent.ShowFeedBackHistory = false;
        },
    }
}
</script>