<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <!-- start page heading -->
            <div class="tab-pane float-left col-12 mb-0 p-0" id="tab_eve_activities">
                                <div class="col-lg-12 mt-0 px-0">
                                    <div class="heading-border mb-2">
                                        <h5 class="h5"><span>{{ $t('RatingAndFeedback') }}</span></h5>
                                    </div>
                                </div>
                                <div class="table-responsive">
                                    <table id="example" class="table table-bordered mb-0" style="width:100%;">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th style="min-width:250px;"><span>{{ $t('Interviewer') }} </span></th>
                                                <th style="min-width:200px;"><span>{{ $t('JOB_TITLE') }} </span></th>
                                                <th style="min-width:200px;"><span>{{ $t('Stage') }} </span></th>
                                                <th style="min-width:150px;"><span>{{ $t('AppliedOn') }} </span></th>
                                                <th style="min-width:150px;"><span>{{ $t('Rating') }} </span></th>
                                                <th style="min-width:150px;"><span>{{ $t('Feedback') }}  </span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="item in Ratingdata">
                                                <td>{{item.FIRST_NAME + ' ' + item.last_name}}</td>
                                                <td>{{item.post_title}}</td>
                                                <td>{{item.stage_name}}</td>
                                                <td>{{item.interview_date}}</td>
                                                <td class="text-primary ">
                                                    <span class="givenrating col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                                        <span id="1" v-bind:class="{'fa fa-star checked':item.rating>=1 && item.rating!=0,'fa fa-star':item.rating<1 || item.rating== 0}"></span>
                                                        <span id="2" v-bind:class="{'fa fa-star checked':item.rating>=2 && item.rating!=0,'fa fa-star':item.rating<2 || item.rating== 0}"></span>
                                                        <span id="3" v-bind:class="{'fa fa-star checked':item.rating>=3 && item.rating!=0,'fa fa-star':item.rating<3 || item.rating== 0}"></span>
                                                        <span id="4" v-bind:class="{'fa fa-star checked':item.rating>=4 && item.rating!=0,'fa fa-star':item.rating<4 || item.rating== 0}"></span>
                                                        <span id="5" v-bind:class="{'fa fa-star checked':item.rating>=5 && item.rating!=0,'fa fa-star':item.rating<5 || item.rating== 0}"></span>
                                                    </span>
                                                </td>
                                                <td v-if=" item.feedback == 'recommended'">{{ $t('Recommended') }} </td>
                                                <td v-if="item.feedback == 'not_recommended'">{{ $t('NotRecommend') }}</td>
                                                <td v-if="item.feedback == 'strongly_recommended'">{{ $t('StronglyRecommend') }} </td>

                                            </tr>
                                            <tr v-if="noRecord">
                                                <td class="text-center text-danger no-record" colspan="5">{{$t("NoRecordfound")}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    <!-- <div class="border-top-pagi">
                                        <pager-control v-bind:numberofrecords="TotalRecords" prev-text="@Resources.Prev" first-text="@Resources.First" last-text="@Resources.Last" next-text="@Resources.Next"></pager-control>
                                    </div> -->
                                </div>
                    </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
export default {
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FromType: '',
            Ratingdata: [],
            SortBy: '',
            SortExp: '',
            dynamicSrc: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN&jobid=${vm.JobId}`;
            await DataService.GetRating(url).then(
                function (response) {
                    vm.Ratingdata = [];
                    if (response.data != null) {
                        if (response.data != "" && response.data.data.length > 0) {
                            vm.Ratingdata = response.data.data;
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.Ratingdata = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.Ratingdata = [];
                    }
                });
        },
    },
}
</script>