<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddTag') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        docArr: {
            type: Object,
            required: true
        },
        docId: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            docId:0,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('DocumentType'),
                            name: "DocumentType",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vm = this;
            if(vm.docId>0){
                (vm.FormSchema)[0].Data[0].value = vm.docArr.row.doc_type;
            }
        },
        Close: function () {
            this.$parent.AddDocumentPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            if (vm.docId > 0) {
            var jsonobj = {
                id: vm.docId == null ? 0 : vm.docId,
                doctext: formfields.DocumentType,
            };
            var params = JSON.stringify(jsonobj);
            DataServices.EditGetDocumentType(params).then((response) => {
                if (response.data.Status == 'Success') {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Document")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Document")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }else{
            var jsonobj = {
                name: formfields.DocumentType
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveDocType(params).then((response) => {
                if (response.data.Status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Document")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Document")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
        }
    }
}
</script>