<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <!-- start page heading -->
            <div class="float-left mb-0 p-0 col-lg-12">

                <dynamic-form ref="DocumentTypeData" lang="en" :buttons="buttons" :schema="FormSchema"
                    v-on:OnSubmit="onSubmit"></dynamic-form>
                <div class="clearfix"></div>
                <div class="row mt-3" v-cloak>
                    <div class="col-sm-12 col-md-12 col-lg-3 col-xl-3 float-left" v-for="(doc, index) in documentList1">
                        <div class="box border pt-4 text-center">
                            <div class="img-ico text-center pb-4 file-image doc-icon pt-3" style="min-height:162px;">
                                <img v-if="doc.path != null && doc.type == 'jpg' || doc.type == 'png' || doc.type == 'jpeg' || doc.type == 'tiff' || doc.type == 'bmp' || doc.type == 'dib' || doc.type == 'ico' || doc.type == 'jfif' || doc.type == 'jpe' || doc.type == 'gif' && doc.type == 'tif'"
                                    v-bind:src="doc.resume_path" alt="DefaultImage" />
                                <span v-else-if="doc.type == 'xlsx' || doc.type == 'xls'">
                                    <em class="fa fa-file-excel-o mt-4 pt-1" aria-hidden="true"></em>
                                </span>
                                <span v-else-if="doc.type == 'pdf'">
                                    <em class="fa fa-file-pdf-o text-danger mt-4 pt-1" aria-hidden="true"></em>
                                </span>
                                <span v-else-if="doc.type == 'docx' || doc.type == 'doc'">
                                    <em class="fa fa-file-word-o text-primary mt-4 pt-1" aria-hidden="true"></em>
                                </span>
                                <span v-else-if="doc.type == 'csv'">
                                    <em class="fa fa-file-text-o text-primary mt-4 pt-1" aria-hidden="true"></em>
                                </span>
                                <span v-else>
                                    <em class="fa fa-file text-primary mt-4 pt-1" aria-hidden="true"></em>
                                </span>
                            </div>
                            <hr class="mb-0">
                            <div class="clearfix"></div>
                            <div class="row mx-0 my-2">
                                <div title="" class="col-md-12 text-left text-truncate font-14 font-weight-bold">
                                    {{ doc.name }}</div>
                                <div class="col-md-12 text-left text-truncate font-14 mb-2 font-weight"> {{
                                    $t('DocumentType') }}: {{ doc.file_type }}</div>
                                <div class="verticle-action">
                                    <em aria-hidden="true" class="fa fa-ellipsis-v action_icon"
                                        @click="openActionIcon($event)"></em>
                                    <span class="action-list-box noneClass" :id="'spnSlider' + index">
                                        <span id="action-list" class="list-actions" style="padding-left: 0px;">

                                            <a data-toggle="modal"
                                                v-on:click="getdownloadfile(doc.path, '', doc.name, doc.file_desc)"
                                                class="actions-onclick edit-list"><i aria-hidden="true"
                                                    class="fa fa-download"></i><br>Download</a>
                                            <a class="actions-onclick view-list"
                                                v-on:click="DeleteAttachment(doc.document_id)">
                                                <em class="fa fa-trash"></em><br>Delete
                                            </a>
                                            <em aria-hidden="true" class="fa fa-times close close-action"></em>
                                        </span>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <ManageDocumentData v-if="AddDocumentPopup" ></ManageDocumentData>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
import ManageDocumentData from '../CandidateProfile/ManageDocumentData.vue'
export default {
components: { ManageDocumentData },
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            interviews: [],
            documentList1: [],
            documenttypes: [],
            item_type: '',
            AddDocumentPopup: false,
            online_url: '',
            fileName: '',
            attachement_mime: '',
            ddljobs: '',
            description: '',
            SortBy: '',
            SortExp: '',
            dynamicSrc: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            extensionName: '',
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('DocumentType'),
                            name: "DocumentType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select Icon'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "FileField",
                            label: this.$t('AttachDocument'),
                            mode: "",
                            name: "AttachDocument",
                            value: "",
                            config: {
                                options: [],
                                maxSize: 5,
                                onChange: this.GetBase64StringCv,
                                multiple: false,
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                showSelectedFiles: []
                            },
                            validationRules: { "required": true },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-primary",
                    text: "<i class='fa fa-save pr-2'></i>Manage Document Type ",
                    onClick: this.ManageDocument
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
            ListData: []
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.ApplicantId = this.$route.params.id;
        vm.containername = this.GetUserInfo.ContainerName;
        await vm.getDocumentList();
        await vm.DocumentTypes();
    },
    methods: {
        getDocumentList: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN`;
            await DataService.GetDocumentsList(url).then(async function (response) {
                if (response.data != null) {
                    vm.Headers = response.data.schema;
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.documentList1 = response.data.data;
                        for (const doc of vm.documentList1) {
                            doc.resume_path = await vm.BindSrc(doc.path);
                        }
                    } else {
                        vm.documentList1 = [];
                    }
                }
                else {
                    vm.documentList1 = [];
                }
            })
        },
        getdownloadfile: async function (path, type, name, attachement_mime) {
            var vm = this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("FileNotFoundForDownload"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    //const fileURL = `data:${((JSON.parse(attachement_mime)).ContentType)};base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        ManageDocument: function (item) {
            var vm =this;
            vm.AddDocumentPopup = !vm.AddDocumentPopup;
        },
        DeleteAttachment: function (val) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Document")), function () {
                var data = {
                    documentId: val,
                    modifiedBy: vm.userId
                };
                var objectPostString = JSON.stringify(data);
                DataService.DeleteDocument(objectPostString).then(function (response) {
                    if (response.status == 200) {
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Document')]), "success", true, vm.$t("Alert"));
                        vm.getDocumentList();
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Document')]), "Success", true, vm.$t("Alert"));
                    }
                });
            });
        },
        BindSrc: async function (filepath) {
            var vm = this;
            var dynamicSrc = '';
            var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
            var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
            let Obj = {
                path: path,
                fileName: fileName
            };
            var url = JSON.stringify(Obj);
            await DataService.GetFileSrc(url).then((response) => {
                if (response.data != '') {
                    dynamicSrc = response.data
                }
            });
            return dynamicSrc;
        },
        onDeleteSelectedFiles: function () {
            this.$delete(this.FormSchema[0].Data[1].config, 'showSelectedFiles');
        },
        openActionIcon(e) {
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        GetBase64StringCv: function (evt) {
            var vm = this;
            vm.isLoading = true;
            var f = evt.target.files[0];
            var reader = new FileReader();
            if (!this.CheckResumeFileExtension(f.name.toLowerCase())) {
                reader.onload = (function (theFile) {
                    return function (e) {
                        var binaryData = '';
                        var base64StringEncoded = '';
                        var fileName = '';
                        var n = '';
                        var extensionName = '';
                        var attachemnetMime = '';
                        fileName = '';
                        vm.attachement_mime = '';
                        vm.fileName = '';
                        vm.base64String = '';
                        vm.isLoading = false;
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            else {
                reader.onload = (function () {
                    return function (e) {
                        var binaryData = e.target.result;
                        var base64StringEncoded = window.btoa(binaryData);
                        var fileName = f.name;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);
                        vm.extensionName = extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        fileName = f.name;
                        vm.attachement_mime = attachemnetMime;
                        vm.fileName = fileName;
                        vm.base64String = base64StringEncoded;
                        vm.isLoading = false;
                    };
                })(f);
                reader.readAsBinaryString(f);
            }
            vm.isLoading = false;
        },
        DocumentTypes: async function () {
            var vm = this;
            vm.ListData = [];
            vm.FormSchema[0].Data[0].config.options = [];
            var url = `id=`;
            await DataService.GetDocumentTypes(url).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.documenttypes = response.data.DATA;
                    response.data.DATA.forEach(item => {
                        vm.ListData.push({
                            value: item.Id,
                            name: item.doc_type
                        })
                    })
                    vm.FormSchema[0].Data[0].config.options = vm.ListData;
                }
                vm.$refs.DocumentTypeData.UpdateKeyValue();
            })
        },
        onSubmit(formfields, val) {
            var vm = this;
            if (vm.fileName == '') {
                var allowedExtensions = ['doc', 'docx', 'jpg', 'pdf', 'csv', 'png', 'xls', 'txt', 'jpeg', 'jfif', 'pjpeg', 'svg', 'pjp'];
                vm.ShowAlert('Invalid file Format. Only' + allowedExtensions.join(', ') + ' are allowed.', "warning", true, vm.$t("Alert"));
                return false;
            }
            var docsdata = "";
            var docsdata = {
                userId: vm.userId,
                companyId: vm.companyId,
                description: vm.description,
                itemImage: vm.fileName,
                giftedByEmialId: '',
                attendeeId: 0,
                attachement_mime: vm.attachement_mime,
                containerName: vm.containername,
                base64String: vm.base64String,
                moduleName: "HIRING",
                subModuleCode: "HR_TALENT_POOL",
                description: vm.fileName,
                Sdoctype: formfields.DocumentType,
                applicantId: vm.ApplicantId
            };
            var postJSON = {
                docsdata: docsdata
            };
            var params = JSON.stringify(postJSON);
            DataService.AddDocuments(params).then((response) => {
                if (response.data.status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Document")]), "success", true, vm.$t("Alert"));
                    (vm.FormSchema)[0].Data[0].value = '';
                    (vm.FormSchema)[0].Data[1].value = '';
                    vm.getDocumentList();
                } else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Document")]), "failure", true, vm.$t("Alert"));
                }
            });
        }
    },
}
</script>