<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Notes') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-Description="{ data }">
                            <ckeditor :editor="editor" v-model="noteDescription"
                                :class="{ 'is-invalid': data.error != '' }">
                            </ckeditor>
                            <span class="invalid-feedback">{{ data.error }}</span>
                        </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        NotesId: {
            type: Number,
            required: true
        },
        NotesData: {
            type: Object,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CompanyId: null,
             editor: this.CLASSIC_EDITOR,
            Userid: null,
            noteDescription: '',
            FormSchema: [
            {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Note Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            visibility: true,
                            validationRules: { "required": true, max: 5000 },
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "CheckBoxField",
                            label: this.$t('Public'),
                            name: "IsPublic",
                            value: "",
                            config: {
                                options: [
                                    { name: '', value: "true" }
                                ]
                            },
                            validationRules: { "required": false },
                        },
                    ],
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        vm.ApplicantId = this.$route.params.id;
        if (vm.NotesId > 0) {
            vm.FetchData();
        }
    },
    methods: {
        FetchData: function () {
            var vm = this;
            (vm.FormSchema)[1].Data[0].value=[];
                vm.noteDescription = vm.NotesData.description;
            (vm.FormSchema)[1].Data[0].value = vm.NotesData.is_public == true ? "true":'';
        },
        Close: function () {
            this.$parent.showAddEditNotesModal = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                NOTES: vm.noteDescription,
                APPLICANT_ID: vm.ApplicantId,
                USER_ID: vm.Userid,
                STATUS_ID: 1001,
                ISPUBLIC: formfields.IsPublic=="true"?true:false,
                ID: vm.NotesId
            };
            var params = JSON.stringify(jsonobj);
            DataServices.AddNotes(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Notes")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.NotesId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Notes")]), "success", true, vm.$t("Alert"));
                            vm.$parent.FetchData();
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Notes")]), "success", true, vm.$t("Alert"));
                            vm.$parent.FetchData();
                        }
                        vm.Close();
                      
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Notes")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>