<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">
            <loader :is-visible="isLoading"></loader>
            <div class="left-menu-tab div-click-event hr_jobpost_a">
                <div class="col-md-12 p-0">
                    <div class="theme-primary partition">
                        <span class="p-name text-white textelipsis">{{ $t('CandidateProfile') }}</span>
                    </div>
                </div>
                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 1 }"
                            data-bs-target="#tab-detail-content" href="javascript:void(0)" data-bs-toggle="tab" role="tab"
                            id="tab-Details" v-on:click="loadData('Personal');">
                            <em class="fa fa-user" aria-hidden="true" aria-selected="true"></em>
                            {{ $t('profile') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 2 }"
                            data-bs-target="#tab-resource-content" href="javascript:void(0)" id="tab-resource-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Resource');">
                            <em class="fa fa-list-alt" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Interview') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 3 }"
                            data-bs-target="#tab-service-content" href="javascript:void(0)" id="tab-service-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('Service');">
                            <em class="fa fa-list-ul" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Notes') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 4 }"
                            data-bs-target="#tab-document-content" href="javascript:void(0)" id="tab-document-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('document');">
                            <em class="fa fa-files-o" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Documents') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 5 }"
                            data-bs-target="#tab-Rating-content" href="javascript:void(0)" id="tab-Rating-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('rating');">
                            <em class="fa fa-calendar" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('Ratings') }}
                        </a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link left-menu" :class="{ 'active show': activeKey == 6 }"
                            data-bs-target="#tab-screening-content" href="javascript:void(0)" id="tab-screening-content"
                            data-bs-toggle="tab" role="tab" v-on:click="loadData('screening');">
                            <em class="fa fa-newspaper-o" aria-hidden="true" aria-selected="false"></em>
                            {{ $t('PreScreeningAssesment') }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="right-content">
                <div class="col-md-12 p-0 mb-2">
                    <div class="theme-primary partition p-actions-expand">
                        <span class="p-actions float-right">
                            <RouterLink to="/HR/CandidateList" class="p-action-btn text-white float-right">
                                <em class="fa fa-angle-double-left"></em>
                                <br />
                                {{ $t('BacktoList') }}
                            </RouterLink>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject && IsHired">
                            <a @click="IsRejectCandidate()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-times"></em>
                                <br />
                                {{ $t('Reject') }}
                            </a>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject && IsHired">
                            <a @click="IsHoldCandidate()" :class="OnHoldClass">
                                <em class="fa fa-hourglass-2"></em>
                                <br />
                                {{ $t('OnHold') }}
                            </a>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject && IsHired">
                            <a @click="IsHireCandidate()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-user"></em>
                                <br />
                                {{ $t('Hire') }}
                            </a>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject && IsActive">
                            <a @click="IsUnHireCandidate()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-user"></em>
                                <br />
                                {{ $t('Un-Hire') }}
                            </a>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject">
                            <a @click="ForRequestDocument()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-file-text"></em>
                                <br />
                                {{ $t('RequestDocuments') }}
                            </a>
                        </span>
                        <span class="p-actions float-right" v-if="isPersonal && IsReject && IsActive && IsHired">
                            <a @click="ForActiveDocument()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-check"></em>
                                <br />
                                {{ $t('Active') }}
                            </a>
                        </span>
                        <span class="p-actions float-right"  v-if="IsReject && IsHired && IsOnHold">
                            <a @click="ScheduleInterview()" class="p-action-btn text-white rqst-doc">
                                <em class="fa fa-clock-o"></em>
                                <br />
                                {{ $t('ScheduleInterview') }}
                            </a>
                        </span>                        
                        <span class="p-actions float-right">
                            <a @click="ManageCandidate()" class="p-action-btn text-white float-right"><em
                                    class="fa fa-pencil"></em><br />
                                {{ $t('Edit') }}</a>
                        </span>

                    </div>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 1 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isPersonal">
                    <CandidateProfileData :candidateData=candidateData :PersonalInfoData="PersonalInfoData"
                        :ApplicantId="ApplicantId" :JobId="JobId"></CandidateProfileData>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 2 }"
                    id="tab-resource-content" role="tabpanel" aria-labelledby="tab-resource-content" v-if="isResource">
                    <InterviewDatamodal :candidateData=candidateData :ApplicantId="ApplicantId" :JobId="JobId">
                    </InterviewDatamodal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 3 }"
                    id="tab-service-content" role="tabpanel" aria-labelledby="tab-service-content" v-if="isService">
                    <NotesDataModal :candidateData=candidateData :JobTitle="JobTitle" :ApplicantId="ApplicantId"
                        :JobId="JobId"></NotesDataModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 4 }"
                    id="tab-document-content" role="tabpanel" aria-labelledby="tab-document-content" v-if="isDocument">
                    <DocumentDataModal :candidateData=candidateData :JobTitle="JobTitle" :ApplicantId="ApplicantId"
                        :JobId="JobId"></DocumentDataModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 5 }"
                    id="tab-Rating-content" role="tabpanel" aria-labelledby="tab-Rating-content" v-if="isRating">
                    <RatingDataModal :candidateData=candidateData :JobTitle="JobTitle" :ApplicantId="ApplicantId"
                        :JobId="JobId"></RatingDataModal>
                </div>
                <div class="tab-pane fade p-0 pb-2 border-0" :class="{ 'active show': activeKey == 6 }"
                    id="tab-screening-content" role="tabpanel" aria-labelledby="tab-screening-content"
                    v-if="isScreeningAssignment">
                    <ScreeningAssesmentDataModel :ApplicantId="ApplicantId" :JobId="JobId"></ScreeningAssesmentDataModel>
                </div>
                <RequestDocumentModel v-if="RequestDocument"></RequestDocumentModel>
                <RejectCandidateModal v-if="RejectCandidate" :OnHoldClick="OnHoldClick"></RejectCandidateModal>
                <modaladdhiring v-if="showhiringcandidate" :hiringFeedbackArr="hiringFeedbackArr"></modaladdhiring>
                <modaladdusermodal v-if="showadduserModal" :hiringFeedbackArr="hiringFeedbackArr"></modaladdusermodal>
            </div>
        </section>
    </div>
</template>
<script>
import NotesDataModal from '../CandidateProfile/NotesDataModal.vue'
import CandidateProfileData from '../CandidateProfile/CandidateProfileData.vue'
import InterviewDatamodal from "../CandidateProfile/InterviewDatamodal.vue"
import DocumentDataModal from '../CandidateProfile/DocumentDataModal.vue'
import RatingDataModal from '../CandidateProfile/RatingDataModal.vue'
import ScreeningAssesmentDataModel from '../CandidateProfile/ScreeningAssesmentDataModel.vue'
import RequestDocumentModel from '../CandidateProfile/RequestDocumentModel.vue'
import RejectCandidateModal from '../CandidateProfile/RejectCandidateModal.vue'
import modaladdhiring from '../CandidateProfile/modaladdhiring.vue'
import modaladdusermodal from '../CandidateProfile/modaladdusermodal.vue'
import DataServices from "@/services/DataService";
export default {
    components: { CandidateProfileData, InterviewDatamodal, NotesDataModal, DocumentDataModal, RatingDataModal, ScreeningAssesmentDataModel, RequestDocumentModel, RejectCandidateModal, modaladdhiring, modaladdusermodal },
    data() {
        return {
            RequestDocument: false,
            RejectCandidate: false,
            showadduserModal: false,
            candidateData: [],
            PersonalInfoData: [],
            isPersonal: false,
            JobTitle: '',
            ApplicantId: 0,
            JobId: 0,
            isResource: false,
            isService: false,
            OnHoldClick: false,
            isDocument: false,
            isRating: false,
            isScreeningAssignment: false,
            NoRecord: false,
            CandidateProfileData: false,
            InterviewDatamodal: false,
            NotesDataModal: false,
            DocumentDataModal: false,
            RatingDataModal: false,
            ScreeningAssesmentDataModel: false,
            tabNameTitle: '',
            hiringFeedbackArr: [],
            CompanyId: null,
            Userid: null,
            showhiringcandidate: false,
            CandidateEmail: '',
            IsReject:true,
            IsActive:true,
            IsHired: true,
            IsOnHold:true,
            OnHoldClass: 'p-action-btn text-white rqst-doc'
        }
    },
    created: async function () {
        var vm=this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        vm.ApplicantId = this.$route.params.id;
        vm.JobId = this.$route.params.JobId;
        await vm.FetchData();
        await vm.GetInterviews();
        vm.loadData("Personal");
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&departmentIds=0&skillIds=0&langCode=EN&jobID=${vm.JobId }`;
            await DataServices.GetCandidateDetails(url).then(
                function (response) {
                    vm.candidateData = [];
                    if (response.data != null && response.data != "" && response.data.data.length > 0) {
                        vm.candidateData = response.data.data;
                        vm.JobTitle = response.data.data[0].POST_TITLE;
                        var StatusCode=response.data.data[0].status_code;
                        if(StatusCode=='Reject'){
                            vm.IsReject=false;
                        }
                        if(StatusCode=='ACT'){
                           vm.IsActive=false;
                        }
                        if(StatusCode=='APPLICANT_REJECTED'){
                            vm.IsReject=false;
                        }
                        if(StatusCode=='HIRED'){
                             vm.IsHired =false;
                        }
                        if(StatusCode=='HOLD'){
                            vm.OnHoldClass='';
                            vm.IsOnHold =false;
                            vm.OnHoldClass='p-action-btn text-white rqst-doc disabled';
                        }
                    }else{
                        vm.IsActive=false;
                    }
                });
            var url = `applicantId=${vm.ApplicantId}&postid=${vm.JobId}`;
            await DataServices.GetPersonalinformation(url).then(
                function (response) {
                    vm.PersonalInfoData = [];
                    if (response.data != null && response.data != "") {
                        vm.PersonalInfoData = response.data.DATA;
                    }
                });
        },
        GetInterviews: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN&jobid=${vm.JobId}`;
            await DataServices.GetInterviews(url).then(
                function (response) {
                    vm.hiringFeedbackArr = [];
                    if (response.data != null) {
                        if (response.data != "" && response.data.data.length > 0) {
                            vm.hiringFeedbackArr = response.data.data;
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.hiringFeedbackArr = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.hiringFeedbackArr = [];
                    }
                });
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            if (tabName == "Personal") {
                vm.isService = false;
                vm.isResource = false;
                vm.isDocument = false;
                vm.isRating = false;
                vm.isScreeningAssignment = false;
                vm.isPersonal = true;
                vm.activeKey = 1;
                vm.openCandidateProfileData();
            } else if (tabName == "Resource") {
                vm.isPersonal = false;
                vm.isService = false;
                vm.isDocument = false;
                vm.isRating = false;
                vm.isScreeningAssignment = false;
                vm.isResource = true;
                vm.activeKey = 2;
                vm.openForInterviewDatamodal();
            } else if (tabName == "Service") {
                vm.isPersonal = false;
                vm.isResource = false;
                vm.isDocument = false;
                vm.isRating = false;
                vm.isScreeningAssignment = false;
                vm.isService = true;
                vm.activeKey = 3;
                vm.openPopupForNotesDataModal();
            } else if (tabName == "document") {
                vm.isPersonal = false;
                vm.isResource = false;
                vm.isService = false;
                vm.isRating = false;
                vm.isScreeningAssignment = false;
                vm.isDocument = true;
                vm.activeKey = 4;
                vm.openPopupDocumentModal();
            }
            else if (tabName == "rating") {
                vm.isPersonal = false;
                vm.isResource = false;
                vm.isService = false;
                vm.isDocument = false;
                vm.isScreeningAssignment = false;
                vm.isRating = true;
                vm.activeKey = 5;
                vm.openPopupRatingModal();
            }
            else if (tabName == "screening") {
                vm.isRating = false;
                vm.isPersonal = false;
                vm.isResource = false;
                vm.isService = false;
                vm.isDocument = false;
                vm.isRating = false;
                vm.isScreeningAssignment = true;
                vm.activeKey = 6;
                vm.openPopupScreeningModal();
            }
        },
        ForRequestDocument: function () {
            var vm = this;
            vm.RequestDocument = !vm.RequestDocument;
        },
        ForActiveDocument: function () {
            var vm = this;
            var messagePositionsfulled = vm.$t("PositionsActive");
            vm.confirmR(messagePositionsfulled, true, 'Reject', function () {
            var obj = {
                    APPLICANTID_VAL: vm.ApplicantId,
                    REASON_VAL: '',
                    POST_ID: vm.JobId,
                    USER_ID: vm.Userid,
                    COMMENTS_VAL: '',
                    IS_NOTIFY_CANDIDATE_VAL: false,
                    IS_NOTIFY_HIRING_TEAM_VAL: false,
                    TYPE_VAL: 'ACTIVE',
                    RATING_VAL: 0,
                    Is_Rating: false,
                    CompanyId: vm.CompanyId
                };
            var objectPostString = JSON.stringify(obj)
            vm.HiringCandidate(objectPostString, 'Active');
        });
        },
        IsRejectCandidate: function () {
            var vm = this;
            vm.OnHoldClick = false;
            vm.RejectCandidate = !vm.RejectCandidate;
        },
        IsHoldCandidate: function () {
            var vm = this;
            vm.OnHoldClick = true;
            vm.RejectCandidate = !vm.RejectCandidate;
        },
        IsHireCandidate: function () {
            var vm = this;
            if (vm.hiringFeedbackArr != null && vm.hiringFeedbackArr != '') {
                vm.showhiringcandidate = true;
            } else {
                vm.confirmR(vm.$t("messagePositionsfulled"), true, 'Confirm', function () {
                    vm.UpdateHiredProfile('HIRED', 'Hire');
                });
            }
        },
        IsUnHireCandidate: function () {
            var vm = this;
            vm.confirmR(vm.$t("messagePositionsUnhired"), true, 'Confirm', function () {
            var url = `applicantId=${vm.ApplicantId}&jobID=${vm.JobId}&langCode=EN&emailId=${vm.CandidateEmail}`;
            DataServices.UnHireCandidate(url).then((response) => {
                if (response.data != null) {
                    if (response.data.CODE == "1") {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("CandidateProfile")]), "success", true, vm.$t("Alert"));
                        vm.IsReject=true;
                        vm.IsActive=false;
                        vm.IsReject=true;
                        vm.IsHired =true;
                        vm.IsOnHold =true;
                    }
                }
            });
        });
        },
        UpdateHiredProfile: async function (type, reqForm) {
            var vm = this;
            var obj = null;
            if (type == 'HIRED') {
                obj = {
                    APPLICANTID_VAL: vm.ApplicantId,
                    REASON_VAL: '',
                    POST_ID: vm.JobId,
                    USER_ID: vm.Userid,
                    COMMENTS_VAL: '',
                    IS_NOTIFY_CANDIDATE_VAL: false,
                    IS_NOTIFY_HIRING_TEAM_VAL: false,
                    TYPE_VAL: 'HIRED',
                    RATING_VAL: 0,
                    Is_Rating: false,
                    CompanyId: vm.CompanyId
                };
            }
            var APPLICANT_COUNT = 0;
            var POSITIONS_COUNT = 0;
            var url = `post_id=${vm.JobId}`;
            await DataServices.GetApplicantCountOnJob(url).then((response) => {
                APPLICANT_COUNT = response.data.APPLICANT_COUNT;
                POSITIONS_COUNT = response.data.POSITIONS_COUNT;
                if (POSITIONS_COUNT > 0 && APPLICANT_COUNT >= POSITIONS_COUNT) {
                    var messagePositionsfulled = vm.$t("Positionsfulfilled") + vm.$t("ConfirmHire");
                    vm.confirmR(messagePositionsfulled, true, 'Hire', function () {
                        var objectPostString = JSON.stringify(obj)
                        vm.HiringCandidate(objectPostString, reqForm);
                    });
                }
                else {
                    var objectPostString = JSON.stringify(obj)
                    vm.HiringCandidate(objectPostString, reqForm);
                }
            });
        },
        HiringCandidate: async function (postString, reqForm) {
            var vm = this;
            await DataServices.ManageApplicantStatus(postString).then((response) => {
                if (response.data.status == "Success") {
                    if (response.data.result == 0) {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("CandidateProfile")]), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        if (reqForm == 'feedback' || reqForm == 'Active') {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("CandidateProfile")]), "success", true, vm.$t("Alert"));
                        } else {
                            //vm.AddcandidateAsUser();
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("CandidateProfile")]), "success", true, vm.$t("Alert"));
                        }
                        if(reqForm=='Active'){
                            vm.IsReject=true;
                            vm.IsActive=false;
                            vm.IsReject=true;
                            vm.IsHired =true;
                            vm.IsOnHold =true;
                            vm.OnHoldClass= 'p-action-btn text-white rqst-doc';
                        }else if(reqForm=='Hire' || reqForm == 'feedback'){
                            vm.IsReject=true;
                            vm.IsActive=true;
                            vm.IsReject=true;
                            vm.IsHired =false;
                            vm.IsOnHold =true;
                        }
                    }
                }
            });
        },
        AddcandidateAsUser: function () {
            this.showadduserModal = true;
        },
        ManageCandidate: function () {
            var vm = this;
            this.$router.push({ name: 'ManageCandidate', params: { reqfrom: 'HR/CandidateList', id: vm.ApplicantId, pid: vm.JobId, aid: null, uid: null } });
        },
        ScheduleInterview:function(){
            var vm =this;
            this.$router.push({ name: 'ScheduleInterview', params: { reqfrom: 'HR/CandidateList', aid: vm.ApplicantId, pid: vm.JobId} });
        },
        openCandidateProfileData: function () {
            var vm = this;
            vm.CandidateProfileData = !vm.CandidateProfileData;
        },
        openForInterviewDatamodal: function () {
            var vm = this;
            vm.InterviewDatamodal = !vm.InterviewDatamodal;
        },
        openPopupForNotesDataModal: function () {
            var vm = this;
            vm.NotesDataModal = !vm.NotesDataModal;
        },
        openPopupDocumentModal: function () {
            var vm = this;
            vm.DocumentDataModal = !vm.DocumentDataModal;
        },
        openPopupRatingModal: function () {
            var vm = this;
            vm.RatingDataModal = !vm.RatingDataModal;
        },
        openPopupScreeningModal: function () {
            var vm = this;
            vm.ScreeningAssesmentDataModel = !vm.ScreeningAssesmentDataModel;
        },
    },
}
</script>