<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <div class="tab-pane float-left col-12 mb-0 p-0 mt-3" id="tab_eve_checklist">
                <div class="col-lg-12 mt-0 px-0">
                    <div class="heading-border mb-2">
                        <h5 class="h5"><span>{{ $t('Notes') }}</span><small class="float-right"><a href="javascript:;"
                                    v-on:click="openNotePopUp()"><em class="fa fa-plus pr-2"></em>{{ $t('AddNoteTab')
                                    }}</a></small></h5>
                    </div>
                </div>
                <div class="row card-view bg-border custom-box-width">
                    <div v-if="!noRecord" class="col-md-12 col-lg-6 col-xl-3 mb-3" v-for="(nt, index) in notesList">
                        <div v-bind:class="nt.is_public == true ? 'card-box blue-bg-light' : 'card-box green-bg-light'">
                            <div class="card-heading">
                                <div class="verticle-action">
                                    <em aria-hidden="true" class="fa fa-ellipsis-v action_icon"
                                        @click="openActionIcon($event)"></em>
                                    <span class="action-list-box noneClass" :id="'spnSlider' + index">
                                        <span id="action-list" class="list-actions" style="padding-left: 0px;">
                                            <a data-toggle="modal" v-on:click="openNotePopUp(nt)"
                                                class="actions-onclick edit-list"><em aria-hidden="true"
                                                    class="fa fa-pencil"></em><br>{{ $t('Edit') }}</a>
                                            <a class="actions-onclick view-list" v-on:click="DeleteNotes(nt)">
                                                <em class="fa fa-trash"></em><br> {{ $t('Delete') }}
                                            </a>
                                            <em aria-hidden="true" class="fa fa-times close close-action"></em>
                                        </span>
                                    </span>
                                </div>
                            </div>
                            <div class="card-detail-main">
                                <div class="card-detail">
                                    <em class="fa fa-cog" aria-hidden="true"></em>
                                    <span class="text-danger" v-if="nt.is_public == true">
                                        {{ $t('Public') }}
                                    </span>
                                    <span class="text-danger" v-else>
                                        {{ $t('Private') }}
                                    </span>
                                </div>
                                <div class="card-date mt-2"><span>Description:</span></div>
                                <div class="card-detail"><span class="spncontent" v-html="nt.description"></span></div>
                            </div>
                            <div class="bottom-action">
                                <div class="card-date">
                                    <em aria-hidden="true" class="fa fa-calendar-o"></em>
                                    <span>&nbsp; {{ formatDate(nt.createdAt) }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div v-if="noRecord" class="col text-center">
                        <span class="text-center text-danger no-record" style="padding: 20px;">{{ $t('NoRecordfound')
                        }}</span>
                    </div>
                </div>
                <modaladdnotes v-if="showAddEditNotesModal" :NotesData="NotesData" :NotesId="NotesId"></modaladdnotes>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
import modaladdnotes from '../../../../components/Common/AddEditNotesModal.vue'
import moment from 'moment'
export default {
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    components: {
        modaladdnotes
    },
    data() {
        return {
            isLoading: false,
            notesList: [],
            NotesData: [],
            SortBy: '',
            SortExp: '',
            showAddEditNotesModal: false,
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
        }
    },
    created: async function () {
        var vm = this;
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containername = this.GetUserInfo.ContainerName;
        await vm.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN&jobid=${vm.JobId}`;
            await DataService.GetNotes(url).then(
                function (response) {
                    vm.notesList = [];
                    if (response.data != null) {
                        if (response.data != "" && response.data.data.length > 0) {
                            vm.notesList = response.data.data;
                            vm.noRecord = false;

                        } else {
                            vm.noRecord = true;
                            vm.notesList = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.notesList = [];
                    }
                });
        },
        openNotePopUp: function (id) {
            var vm = this;
            vm.NotesData = [];
            if (id == undefined) {
                vm.NotesId = 0;
            } else {
                vm.NotesData = id;
                vm.NotesId = id.note_id;
            }
            this.showAddEditNotesModal = !this.showAddEditNotesModal;
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, "YYYY-MM-DD").format(String('MM/DD/YYYY'))
            }
        },
        DeleteNotes: function (item) {
            var vm = this;
            var val = item.note_id;
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_job_applicant_note&key=note_id`;
                    DataService.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Notes')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Notes')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
        },
        openActionIcon(e) {
            e.target.nextSibling.removeAttribute('style');
            e.target.nextSibling.setAttribute('style', 'width:240px;');
            e.target.nextSibling.style.display = 'block';
        },
        Close: function () {
            var vm = this;
            this.$router.push({ name: 'CandidateList' });
        },
    },
}
</script>