<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ManageRejectReason') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="Area" :LegendArray="[]"
                                :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                                :widgets="[]" IdentityColumn="reason_id" :listType="['List']" :ListData="RejectReasonArray"
                                :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
                                :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp"
                                :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                                v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                                <template v-slot:slotdata="{ data }">
                                <template v-if="data.column.COLUMN_NAME == 'reason'">
                                    <a href="#" @click="OpenAddPostReasonPopup(data)">{{
                                        data.row.reason }}</a>
                                </template>
                            </template>
                            </tg-list>
                            <div class="modal-footer">
                                <a href="javascript:;" v-on:click="Close" class="btn btn-danger formbtn">
                                    <i class="fa fa-times"></i> {{ $t('Close') }}
                                </a>
                            </div>
                    </div>
                </div>
            </div>
        </div>
        <AddPostReasonPopup v-if="AddPostReasonPopup" :ReasonArr="ReasonArr" :ReasonId="ReasonId"></AddPostReasonPopup>  
    </div>
    </template>
    <script>
   import DataServices from "@/services/DataService";
    import Schema from '../../Candidate/SkilSchema'
    import AddPostReasonPopup from '../../Candidate/CandidateProfile/AddPostReasonPopup.vue'
    export default {
        components: { AddPostReasonPopup },
        data(){
            return{
                isLoading:false,
                HeaderText:'',
                ReasonId:0,
                ReasonArr:[],
                noRecords: false,
                AddPostReasonPopup: false,
                listheaderbuttons: Schema.buttons,
                Headers:[{
                    "COLUMN_NAME": "reason",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": 'Reject Reason ',
                    "DISPLAY_ORDER": 1,
    
                }], 
                SortBy:'',          
                skillStringArr:[],
                SortExp:'',
                TotalRecords: 0,
                searchCondition:'',
                PageNumber: 1,
                PageSize:10,         
                RejectReasonArray: [],           
                CurrentPage: 1,
                TotalPages: 0,    
                skillString: '',
            }
        },
    created: function () {
        var vm = this;
        vm.FetchData();
    },
    methods: {
        renderActions: function (row) {

        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "openAddSkill":
                    this.OpenAddPostReasonPopup();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
            }
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Reason")), function () {
                    var url = `ids=${val}&tablename=talygen_hr_reason&key=reason_id`;
                    DataServices.DeleteItems(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Reason')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Reason')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {

        },
        OpenAddPostReasonPopup: function (item) {
            var vm =this;
            if (item == undefined){
                vm.ReasonId=0;
                vm.ReasonArr = [];
            }else{
                vm.ReasonId=item.row.reason_id;
                vm.ReasonArr = item;
            }
            vm.AddPostReasonPopup = !vm.AddPostReasonPopup;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        FetchData: async function () {
            var vueObj = this;
            var params = `pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            await DataServices.ManageRejectReason(params).then((response) => {
                if (response.data.DATA.length > 0) {
                    vueObj.Headers.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "reason") {
                            column.settings = {
                                isInSlot: true,
                            };
                        }
                    });
                    vueObj.RejectReasonArray = response.data.DATA;
                    if (vueObj.RejectReasonArray.length > 0) {
                        vueObj.TotalRecords = response.data.DATA[0].TotalRecords;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.listheaderbuttons[1].isdisabled = true;
                        vueObj.noRecords = false;
                    }
                    else {
                        vueObj.noRecords = true;
                    }
                }
                else {
                    vueObj.noRecords = true;
                    vueObj.TotalRecords = 0
                }
            })
        },
        Close:function(){
                var vm =this;
                vm.$parent.GetRejectReasonList();
                this.$parent.AddReason=false;
            }
    }
}
</script>
    
    
    