<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ReviewFeedBack') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-md-4" v-for="intrv in hiringFeedbackArr ">
                                <div class="float-left">
                                    <div class="user-image" title="ProfileImage">
                                        <img class="hiring_cantidate_img_pop" v-bind:src="BindSrc(intrv.avatar)"
                                            alt="ProfileImage"
                                            onerror="this.src = '/Content/images/DefaultImages/avatar-icon.jpg'"
                                            loading="lazy" />
                                    </div>
                                </div>
                                <div class="float-left">
                                    <div class="candidate-data">
                                        <b>{{ intrv.FIRST_NAME }} {{ intrv.last_name }}</b><small
                                            class="ml-1 p-1 bg-primary text-white">
                                            {{ intrv.designation_name }}</small>
                                        <div class="text-success"><em class="fa fa-cog" aria-hidden="true"></em>
                                            <span class=""><b>{{$t('StageName') }}: </b>{{ intrv.stage_name }}</span>
                                        </div>
                                        <div v-if="intrv.feedback == 'recommended'"><span class=""><b>{{$t('Feedback') }}:</b>
                                            {{$t('Recommended') }}   </span></div>
                                        <div v-if="intrv.feedback == 'not_recommended'"><span class=""><b>
                                            {{$t('Feedback') }}:</b> {{$t('NotRecommended') }}</span></div>
                                        <div v-if="intrv.feedback == 'strongly_recommended'"><span class=""><b>
                                            {{$t('Feedback') }} :</b> {{$t('StronglyRecommend') }} </span></div>
                                        <div v-if="intrv.feedback == '' || intrv.feedback == null"><span class=""><b>
                                            {{$t('Feedback') }} :</b> {{$t('Pending') }}  </span></div>
                                        <div class="clearfix"></div>
                                        <span></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <a href="javascript:;" class="btn btn-success text-white" v-on:click="CheckCondition()"><em
                                class="fa fa-save pr-2"></em><span>{{$t('Continue') }}  </span></a>
                        <a href="javascript:;" class="btn btn-danger" v-on:click="Close"><em
                                class="fa fa-times pr-2"></em><span>{{$t('Cancel') }} </span></a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        hiringFeedbackArr: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            CompanyId: null,
            Userid: null,
            viewType: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.CompanyId = this.GetUserInfo.CompanyId;
        vm.Userid = this.GetUserInfo.ID;
        vm.ApplicantId = this.$route.params.id;
    },
    methods: {
        CheckCondition: function () {
            var vm = this;
            if (vm.hiringFeedbackArr != null && vm.hiringFeedbackArr != '') {
                for (var i = 0; i < vm.hiringFeedbackArr.length; i++) {
                    if (vm.hiringFeedbackArr[i].feedback == "not_recommended") {
                        vm.viewType = "not_recommended";
                        break;
                    }
                    if (vm.hiringFeedbackArr[i].feedback == null || vm.hiringFeedbackArr[i].feedback == '') {
                        vm.viewType = "pending";
                        break;
                    }
                    if (vm.hiringFeedbackArr[i].feedback == "recommended" || vm.hiringFeedbackArr[i].feedback == "strongly_recommended") {
                        vm.viewType = "recommended";
                    }
                }
                vm.openCondtionalModal(vm.viewType);
            }
            else {
                vm.openCondtionalModal("not provided");
            }
        },
        openCondtionalModal: function (type) {
            var vm=this;
            var Interviewrsname = '';
            if (type != "recommended") {
                if (type == "not provided") {
                    vm.confirmR(vm.$t("messagePositionsfulled"), true, 'Confirm', function () {
                        vm.$parent.UpdateHiredProfile("HIRED", "feedback");
                        vm.Close();
                    });
                }
                if (type == "pending") {
                    var Interviewrsname = '';
                    for (var i = 0; i < vm.hiringFeedbackArr.length; i++) {
                        if (vm.hiringFeedbackArr[i].feedback == "pending" || vm.hiringFeedbackArr[i].feedback == null || vm.hiringFeedbackArr[i].feedback == '') {
                            if (Interviewrsname == '') {
                                Interviewrsname = vm.hiringFeedbackArr[i].FIRST_NAME;
                            }
                            else {
                                Interviewrsname = Interviewrsname + ', ' + vm.hiringFeedbackArr[i].FIRST_NAME;
                            }
                        }
                    }
                    var messagePositionsfulled = "The interviewer feedback is still pending by " + Interviewrsname + ". Press Ok If you still want to hire the candidate";
                    vm.confirmR(messagePositionsfulled, true, 'Hire', function () {
                        vm.$parent.UpdateHiredProfile("HIRED", "feedback");
                        vm.Close();
                    });
                }
                if (type == "not_recommended") {

                    for (var i = 0; i < vm.hiringFeedbackArr.length; i++) {
                        if (vm.hiringFeedbackArr[i].feedback == "not_recommended") {
                            if (Interviewrsname == '') {
                                Interviewrsname = vm.hiringFeedbackArr[i].FIRST_NAME;
                            }
                            else {
                                Interviewrsname = Interviewrsname + ', ' + vm.hiringFeedbackArr[i].FIRST_NAME;

                            }
                        }
                    }
                    var messagePositionsfulled = "The Candidate is not recommended by the " + Interviewrsname + ". Press Ok If you still want to hire the candidate";
                    vm.confirmR(messagePositionsfulled, true, 'Hire', function () {
                        vm.$parent.UpdateHiredProfile("HIRED", "feedback");
                        vm.Close();
                    });
                }

            }
            else {
                vm.confirmR(vm.$t("ConfirmHire"), true, 'Hire', function () {
                    vm.$parent.UpdateHiredProfile("HIRED", "feedback");
                    vm.Close();
                });
            }
        },
        BindSrc: async function (filepath) {
            var vm = this;
            var dynamicSrc = '';
            var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
            var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
            let Obj = {
                path: path,
                fileName: fileName
            };
            var url = JSON.stringify(Obj);
            await DataService.GetFileSrc(url).then((response) => {
                if (response.data != '') {
                    dynamicSrc = response.data
                }
            });
            return dynamicSrc;
        },
        Close: function () {
            var vm = this;
            vm.$parent.showhiringcandidate = false;
        },
    }
}
</script>