<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <!-- start page heading -->
            <div class="tab-pane float-left col-12 mb-0 p-0" id="tab_eve_dashboard">
                <div class="col-lg-12 mt-0 px-0" v-if="this.rejectinterviews != ''"
                    v-for="(intrv, index) in rejectinterviews">
                    <h5 class="h5"><span><b>{{ index + 1 }}st Round:</b></span></h5>
                    <h5 class="h5"><span>{{ $t('DeclineOrRejected') }}</span></h5>
                    <div class="card-detail"><span><b>{{ $t('Reason') }}: </b>{{ intrv.reason }} </span></div>
                    <div class="card-detail"><span><b>{{ $t('Comment') }}: </b>{{ intrv.comments }}</span></div>
                    <br />
                </div>
                <div class="col-lg-12 mt-0 px-0" v-if="this.interviews != ''">
                    <div class="heading-border mb-3">
                        <h5 class="h5"><span>{{ $t('InterviewFor') }} {{ this.interviews[0].post_title }}</span></h5>
                    </div>
                </div>
                <div class="row card-view bg-border" v-if="this.interviews != ''">
                    <div class="col-md-12 col-lg-6 col-xl-3 mb-3" v-for="intrv in interviews">
                        <div class="card-box green-bg-light">
                            <div class="card-heading d-flex align-items-center">
                                <span class="user-img-con mr-2">
                                    <img v-bind:src="bindsrcUser(intrv.avatar)" alt="ProfileImage"
                                        onerror="this.src = '/Content/images/DefaultImages/avatar-icon.jpg'"
                                        loading="lazy" />
                                </span>
                                {{ intrv.FIRST_NAME }} {{ intrv.last_name }}<small class="text-primary ml-1">|
                                    {{ intrv.designation_name }}</small>
                            </div>
                            <div class="card-detail-main">
                                <div class="card-detail font-weight-bold"><em class="fa fa-cog" aria-hidden="true"></em>
                                    <span>{{ intrv.stage_name }}</span>
                                </div>
                                <div class="card-date"><em class="fa fa-calendar-o" aria-hidden="true"></em>&nbsp;
                                    <span>{{ intrv.interview_date }} @ {{ new Date(intrv.interview_date + " "+intrv.interview_from_time).toLocaleTimeString() }} </span>
                                </div>
                                <div class="card-detail"><em class="fa fa-hourglass-o" aria-hidden="true"></em><span>{{timeConvert(new Date(intrv.interview_date+" "+intrv.interview_to_time),new Date(intrv.interview_date+" "+intrv.interview_from_time))}}</span></div>
                                <div class="card-detail" v-if="intrv.feedback == 'recommended'"><span
                                        class="text-primary ml-1">{{ $t('Feedback') }}: {{ $t('Recommended') }}</span></div>
                                <div class="card-detail" v-if="intrv.feedback == 'not_recommended'"><span
                                        class="text-primary ml-1"> {{ $t('Feedback') }}: {{ $t('NotRecommend') }}</span></div>
                                <div class="card-detail" v-if="intrv.feedback == 'strongly_recommended'"><span
                                        class="text-primary ml-1"> {{ $t('Feedback') }}: {{ $t('StronglyRecommend') }}</span></div>
                            </div>
                            <div class="bottaom-action">
                                <div class="card-date">
                                    <span class="cntct mt-1 text-danger d-block feedback">
                                        <span class="font-weight-normal text-dark">
                                            <a v-if="intrv.comment == null" href="javascript:;"
                                                v-on:click="(intrv.interview_date != '' && intrv.interview_date != null) ? openPopUp(intrv.recruiter_id, intrv.interview_job_stage_id) : ''"
                                                class="text-bold text-success ml-2 ">{{ $t('SubmitFeedback') }}</a>

                                        </span>
                                        <a v-if="(intrv.comment != null)" href="javascript:;"
                                            v-on:click="openview(intrv.recruiter_id, intrv.interview_job_stage_id)"
                                            class="text-bold text-success ml-2">{{ $t('ViewFeedback') }} </a>
                                    </span>
                                </div>
                                <div class="card-action">
                                    <a v-if="intrv.comment != null" href="javascript:;"
                                        v-on:click="openPopUp(intrv.recruiter_id, intrv.interview_job_stage_id)"
                                        class="round-icon-small btn-success-light  "><em aria-hidden="true"
                                            class="fa fa-pencil"></em></a>
                                </div>
                                <div class="card-date">
                                    <span class="cntct mt-1 text-danger d-block feedback">
                                        <a v-if="(intrv.comment != null)" href="javascript:;"
                                            v-on:click="openviewHistory(intrv.recruiter_id, intrv.interview_job_stage_id)"
                                            class="text-bold text-success ml-2">{{ $t('ViewFeedbackHistory') }} </a>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-if="noRecord" class="col text-center">
                    <span class="text-center text-danger no-record" style="padding: 20px;">{{ $t('NoRecordfound')
                    }}</span>
                </div>
            <AddEditFeedback v-if="ShowAddEditFeedback" :FeedbackData="FeedbackData" :ApplicantId="ApplicantId" :JobId="JobId" :RecuiterId="RecuiterId" :InterviewStageId="InterviewStageId" :FromType="FromType"></AddEditFeedback>
            <FeedBackHistory v-if="ShowFeedBackHistory" :FeedbackData="FeedbackData" :ApplicantId="ApplicantId" :JobId="JobId" :RecuiterId="RecuiterId" :InterviewStageId="InterviewStageId" :FromType="FromType"></FeedBackHistory>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from "@/services/DataService";
import AddEditFeedback from '../CandidateProfile/AddEditFeedback.vue'
import FeedBackHistory from '../CandidateProfile/FeedBackHistory.vue'
export default {
    components: { AddEditFeedback, FeedBackHistory },
    props: {
        ApplicantId: {
            type: Number,
            required: true
        },
        JobId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FromType: '',
            interviews: [],
            ShowAddEditFeedback: false,
            ShowFeedBackHistory: false,
            rejectinterviews: [],
            FeedbackData:[],
            SortBy: '',
            SortExp: '',
            dynamicSrc: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            RecuiterId:0,
            InterviewStageId: 0
        }
    },
    created: async function () {
        var vm = this;
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containername = this.GetUserInfo.ContainerName;
        await vm.FetchData();
        await vm.GetRejectCandiateInverView();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN&jobid=${vm.JobId}`;
            await DataService.GetInterviews(url).then(
                function (response) {
                    vm.interviews = [];
                    if (response.data != null) {
                        if (response.data != "" && response.data.data.length > 0) {
                            vm.interviews = response.data.data;
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.interviews = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.interviews = [];
                    }
                });
        },
        openPopUp:async function (recuiterId, interview_job_stage_id) {
            var vm=this;
            vm.FromType= '';
            if (recuiterId == undefined){
                vm.RecuiterId = 0;
                vm.InterviewStageId=0;
            }else{
                vm.RecuiterId = recuiterId;
                vm.InterviewStageId=interview_job_stage_id;
            }
            await vm.getfeedback();
            vm.ShowAddEditFeedback = !vm.ShowAddEditFeedback;
            
        },
        openview:async function (recuiterId, interview_job_stage_id) {
            var vm=this;
            vm.FromType= 'OpenView';
            if (recuiterId == undefined){
                vm.RecuiterId = 0;
                vm.InterviewStageId=0;
            }else{
                vm.RecuiterId = recuiterId;
                vm.InterviewStageId=interview_job_stage_id;
            }
            await vm.getfeedback();
            vm.ShowAddEditFeedback = !vm.ShowAddEditFeedback;
        },
        openviewHistory:async function (recuiterId, interview_job_stage_id) {
            var vm=this;
            if (recuiterId == undefined){
                vm.RecuiterId = 0;
                vm.InterviewStageId=0;
            }else{
                vm.RecuiterId = recuiterId;
                vm.InterviewStageId=interview_job_stage_id;
            }
            vm.ShowFeedBackHistory = !vm.ShowFeedBackHistory;
        },
        
        getfeedback: async function(){
            var vm=this;
            var url = `applicantId=${vm.ApplicantId}&postId=${vm.JobId}&interviewerid=${vm.RecuiterId}&stageid=${vm.InterviewStageId}&langCode=EN`;
            await DataService.GetFeedback(url).then(
                function (response) {
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.FeedbackData=  response.data.data[0];
                    } else {
                         vm.FeedbackData = [];
                    }
                });
        },
        bindsrcUser: async function (filepath) {
            if (filepath == null || filepath == undefined) {
                return "~/Content/images/DefaultImages/avatar-icon.jpg"
            } else {
                var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
                var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
                let Obj = {
                    path: path,
                    fileName: fileName
                };
                var url = JSON.stringify(Obj);
                var dynamicSrc = '';
                await DataService.GetFileSrc(url).then((response) => {
                    if (response.data != '') {
                        dynamicSrc = response.data
                    }
                });
                return dynamicSrc;
            }
        },
        timeConvert: function (to, from) {
            var diff = (to.getTime() - from.getTime()) / 1000;
            diff /= 60;
            diff = Math.abs(Math.round(diff));
            var num = diff;
            var hours = (num / 60);
            var rhours = Math.floor(hours);
            var minutes = (hours - rhours) * 60;
            var rminutes = Math.round(minutes);
            if (rhours != 0) {
                return rhours + "hours: " + rminutes + " minutes";
            }
            else {
                return rminutes + " minutes";
            }
        },
        GetRejectCandiateInverView: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN&jobid=${vm.JobId}`;
            await DataService.GetRejectInverView(url).then(
                function (response) {
                    vm.rejectinterviews = [];
                    if (response.data != null) {
                        if (response.data != "" && response.data.data.length > 0) {
                            vm.rejectinterviews = response.data.data;
                        } else {
                            vm.rejectinterviews = [];
                            vm.TotalRecords = 0
                        }
                    }
                    else {
                        vm.rejectinterviews = [];
                    }
                });
        },
        Close: function () {
            var vm = this;
            this.$router.push({ name: 'CandidateList' });
        },
    },
}
</script>